.block1 {
  display: flex;
  
  justify-content: space-around;
  background-color: #2ac0e2;
  background-image: linear-gradient(19deg, #2db4d3 0%, #bc43f5 87%);
  height: 60vh;
  box-shadow: 0px 15px 20px -15px #111;
}

.text {
  flex: 1;
  padding: 40px;
  display: flex;
  flex-direction: column;

  text-align: left;
  padding: 60px 0px;
  font-size: 15px;
}

.text a {
  font-size: 15px;
  background-color: rgb(255, 145, 0);
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border: none;
  width: fit-content;
  padding: 10px;
  border-radius: 15px;
  text-decoration: none;
  color: white;
}

.text a:hover {
  background-color: rgb(247, 63, 30);
  color: white;
}

.text h2 {
  max-width: 90%;
  font-size: 40px;
  color: rgb(227, 230, 238);
}

.text p {
  max-width: 70%;
  font-size: 15px;
  color: rgb(227, 230, 238);
}

.image_box {
  flex: 1;
  padding: 50px;
}

@media (max-width: 988px) {
  .text h2 {
    font-size: 35px;
  }
  .text p {
    font-size: 15px;
  }
}

@media (max-width: 849px) {
  .image_box {
    
    padding: 0px auto;
    
    display: flex;
  justify-content: center;
  padding-bottom: 0px;
  }
  .text {
    margin-top: 0px;
    text-align: center;
    padding-top: 1rem;
    padding-bottom: 0px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    justify-items: center;
    
  }
  .image_box img {
    width: 100%;
    max-height: 40%;

    
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    border-radius: 10px;
  }
  .text h2 {
    font-size: 30px;
    text-align: center;
    margin: 0 auto;
    
  }
  .text p {
    font-size: 15px;
    text-align: center;
    margin: 0 auto;
    margin-top: 1rem;
  }
  .text a {
    font-size: 10px;
    width: 50%;
    width: fit-content;
  margin: 0 auto;
  margin-top: 1rem;
  }
  .block1{
    height: fit-content;
    display: flex;
    flex-direction: column;
    padding-bottom: 1.5rem;
    
  }
}

@media (max-width: 767px) {
  .text h2 {
    font-size: 27px;
  }
  .text p {
    font-size: 12px;
  }
}
@media (min-width: 1300px) {
  .text h2 {
    padding-bottom: 2%;
  }
  .text p {
    padding-bottom: 2%;
  }
}



.image_box img {
  width: 80%;
  max-height: 80%;

  margin: 1rem 1rem;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 15px;
}

.sous_block div img {
  border-radius: 15px;
}

.block2 {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding-top: 30px;
}

.sous_block {
  padding: 2%;
  flex: 1;
  height: 50vh;
}

.sous_block img {
  width: 100%;
}

.explication {
  display: flex;
  flex-direction: column;
  text-align: center;
  padding-top: 10px;
}

.explication h3 {
  font-size: 25px;
}

.explication p {
  font-size: 15px;
}

@media (max-width: 1199px) {
  .explication h3 {
    font-size: 20px;
  }
  .explication p {
    font-size: 15px;
  }
}

@media (max-width: 959px) {
  .explication h3 {
    font-size: 18px;
  }
  .explication p {
    font-size: 12px;
  }
}

@media (max-width: 471px) {
  .explication h3 {
    font-size: 16px;
  }
  .explication p {
    font-size: 10px;
  }
}
